// 方法
/* 圖片取代文字技巧 */
@mixin hide-text {
    text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
  }


//斷點
@mixin desktop {
    @media (max-width: 1320x) {
        @content
    }
}
@mixin pad() {
    @media (max-width: 720px) {
        @content
    }
}

@mixin mobile() {
    @media (max-width: 568px) {
        @content
    }
}

@mixin mobile320() {
    @media (max-width: 320px) {
        @content
    }
}